<style scoped>
  nav{
    background: crimson;
    padding: 30px 0;
    margin-bottom: 40px;
  }
  ul{
    list-style-type: none;
    text-align: center;
    margin:0;
  }
  li{
    display: inline-block;
    margin:0 10px;
  }
  a{
    color: #fff;
    text-decoration: none;
    padding: 12px;
    border-radius: 5px;
  }
  .router-link-active{
