<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />
      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="cheakboxs">
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>Vue.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />
        <label>Node.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories" />
        <label>React.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories" />
        <label>Angular4.js</label>
        <select v-model="blog.author">
          <option v-for="author in authors">
            {{author}}
          </option>
        </select>
        <button v-on:click.prevent="submit">编辑博客</button>
      </div>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功</h3>
    </div>

    <div id="preview">
      <h3>博客总览：</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="category in blog.categories">
          <p>{{category}}</p>
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "edit-blog",
    data(){
      return{
        id:this.$route.params.id,
        blog:{},
        authors:[
          'ZhangJian','LiuWei','TangYuan'
        ],
        submmited:false
      }
    },
    methods:{
      fetchData(){
          //console.log(this.id);
        axios.get('/posts/' + this.id +'.json')
          .then(response =>{
            console.log(response);
            this.blog = response.data
          })
      },

      submit(){
        axios.put('/posts/' + this.id +'.json',this.blog)
          .then((data)=>{
            //console.log(data);
            this.submmited=true
          })
      }
    },
    created(){
        this.fetchData();
    }
  }
</script>

<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    max-width: 600px;
    margin:20px auto;
    padding:20px;
  }
  label{
    display: block;
    margin:20px 0 10px;
  }
  input[type='text'],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }
  textarea{
    height: 200px;
  }
  #cheakboxs label{
    display: inline-block;
    margin:0;
  }
  #cheakboxs input{
    display: inline-block;
    margin-right:0;
  }
  button{
    display:block;
    margin:20px;
    background: crimson;
    color: #fff;
    font-size: 18px;
    border: 0;
    cursor: pointer;
    padding: 14px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
  }
  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin:30px 0;
  }
</style>



